// API configuration
const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8001/api';

export interface PlateDetectionResponse {
  message: string;
  plate?: string;
}

export interface PlateDetectionError {
  error: string;
}

/**
 * Upload plate image to the backend API for processing
 * @param file - The image file to be processed
 * @returns Promise with the detection result
 */
export async function uploadPlateImage(file: File): Promise<PlateDetectionResponse> {
  const formData = new FormData();
  formData.append('plate_image', file);

  try {
    const response = await fetch(`${API_BASE_URL}/plate-detect`, {
      method: 'POST',
      body: formData,
      headers: {
        // Don't set Content-Type header when using FormData
        // The browser will set it automatically with the correct boundary
      },
    });

    const data = await response.json();

    if (response.ok) {
      // Success response (200)
      return {
        message: data.message || 'Saved in the system',
        plate: data.plate
      };
    } else if (response.status === 404) {
      // Plate not found or invalid
      throw new Error(data.error || 'Plate not found or invalid plate format');
    } else if (response.status === 422) {
      // Validation error
      throw new Error(data.error || 'Invalid image file or format');
    } else {
      // Other errors
      throw new Error(data.error || 'Unexpected error occurred');
    }
  } catch (error) {
    console.error('Error uploading image:', error);
    
    if (error instanceof TypeError && error.message.includes('fetch')) {
      // Network error
      throw new Error('Unable to connect to the server. Please check your connection.');
    }
    
    // Re-throw the error if it's already a custom error message
    throw error;
  }
}

/**
 * Health check for the API
 * @returns Promise with the API status
 */
export async function checkApiHealth(): Promise<boolean> {
  try {
    const response = await fetch(`${API_BASE_URL}/health`, {
      method: 'GET',
    });
    return response.ok;
  } catch (error) {
    console.error('API health check failed:', error);
    return false;
  }
}

/**
 * Get API configuration info
 * @returns API configuration object
 */
export function getApiConfig() {
  return {
    baseUrl: API_BASE_URL,
    endpoints: {
      plateDetect: `${API_BASE_URL}/plate-detect`,
      health: `${API_BASE_URL}/health`
    }
  };
}
