"use client";

import React, { useRef, useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Camera, Upload, CheckCircle, AlertCircle, Loader2 } from 'lucide-react';
import { uploadPlateImage } from '@/lib/api';

export default function PlateScanner() {
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [previewImage, setPreviewImage] = useState<string | null>(null);
  const [message, setMessage] = useState<string>('');
  const [messageType, setMessageType] = useState<'success' | 'error' | ''>('');
  const [loading, setLoading] = useState<boolean>(false);
  const [extractedPlate, setExtractedPlate] = useState<string>('');

  const handleScanClick = () => {
    if (fileInputRef.current) {
      fileInputRef.current.click();
    }
  };

  const handleFileChange = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const files = event.target.files;
    if (files && files[0]) {
      const file = files[0];
      
      // Validate file type
      if (!file.type.startsWith('image/')) {
        setMessage('Please select a valid image file.');
        setMessageType('error');
        return;
      }

      // Validate file size (max 5MB)
      if (file.size > 5 * 1024 * 1024) {
        setMessage('File size must be less than 5MB.');
        setMessageType('error');
        return;
      }

      // Set preview
      const imageUrl = URL.createObjectURL(file);
      setPreviewImage(imageUrl);
      setLoading(true);
      setMessage('');
      setMessageType('');

      try {
        const result = await uploadPlateImage(file);
        setMessage(result.message);
        setExtractedPlate(result.plate || '');
        setMessageType('success');
      } catch (error: any) {
        setMessage(error.message || 'An error occurred during scan.');
        setMessageType('error');
        setExtractedPlate('');
      } finally {
        setLoading(false);
      }
    }
  };

  const resetScanner = () => {
    setPreviewImage(null);
    setMessage('');
    setMessageType('');
    setExtractedPlate('');
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  return (
    <Card className="w-full shadow-xl bg-white/95 backdrop-blur-sm">
      <CardHeader className="text-center">
        <CardTitle className="text-2xl font-bold text-gray-900 flex items-center justify-center gap-2">
          <Camera className="h-6 w-6 text-blue-600" />
          License Plate Scanner
        </CardTitle>
        <CardDescription className="text-gray-600">
          Capture or upload an image of a Saudi Arabia license plate
        </CardDescription>
      </CardHeader>
      
      <CardContent className="space-y-4">
        {/* Image Preview */}
        {previewImage && (
          <div className="relative">
            <img
              src={previewImage}
              alt="Scanned plate preview"
              className="w-full h-48 object-cover rounded-lg border-2 border-gray-200"
            />
            <Button
              variant="outline"
              size="sm"
              onClick={resetScanner}
              className="absolute top-2 right-2 bg-white/90 hover:bg-white"
            >
              Reset
            </Button>
          </div>
        )}

        {/* Extracted Plate Display */}
        {extractedPlate && (
          <div className="bg-blue-50 border border-blue-200 rounded-lg p-4 text-center">
            <p className="text-sm text-blue-600 font-medium">Detected Plate Number:</p>
            <p className="text-2xl font-bold text-blue-900 font-mono tracking-wider">
              {extractedPlate}
            </p>
          </div>
        )}

        {/* Scan Button */}
        <div className="flex flex-col gap-3">
          <Button 
            onClick={handleScanClick}
            disabled={loading}
            className="w-full h-12 text-lg font-semibold bg-blue-600 hover:bg-blue-700 disabled:opacity-50"
          >
            {loading ? (
              <>
                <Loader2 className="mr-2 h-5 w-5 animate-spin" />
                Processing...
              </>
            ) : (
              <>
                <Upload className="mr-2 h-5 w-5" />
                Scan License Plate
              </>
            )}
          </Button>
          
          <input
            ref={fileInputRef}
            type="file"
            accept="image/*"
            capture="environment"
            onChange={handleFileChange}
            className="hidden"
          />
        </div>

        {/* Status Message */}
        {message && (
          <Alert className={messageType === 'success' ? 'border-green-200 bg-green-50' : 'border-red-200 bg-red-50'}>
            {messageType === 'success' ? (
              <CheckCircle className="h-4 w-4 text-green-600" />
            ) : (
              <AlertCircle className="h-4 w-4 text-red-600" />
            )}
            <AlertDescription className={messageType === 'success' ? 'text-green-800' : 'text-red-800'}>
              {message}
            </AlertDescription>
          </Alert>
        )}

        {/* Instructions */}
        <div className="text-center text-sm text-gray-500 space-y-1">
          <p>• Ensure the license plate is clearly visible</p>
          <p>• Good lighting improves detection accuracy</p>
          <p>• Supported formats: JPG, PNG, WebP</p>
        </div>
      </CardContent>
    </Card>
  );
}
